import KS_Utils from 'utils/index.js'

import {
	UniPopup
} from 'components/uni/uni-popup/uni-popup'
import {UniLoadMore} from 'components/uni/uni-load-more/uni-load-more'
import {
	GetBj,
	SaveBj
} from 'api/course'
export default {
	components: {
		UniPopup,
		UniLoadMore
	},
	props: {
		periodId: Number,
		courseId: Number,
		pageChange:Number
	},
	data() {
		return {
			active: 0,
			list: [],
			val: '',
			isShare: false,
			state: {},
			show: false,
			page:1,
			pageCount:1,
			moreStatus:''
		}
	},
	watch: {
		// pageChange(val) {
		// 	this.reachBottom()
		// }
	},
	methods: {
		/*滚动加载*/
		reachBottom() {
			this.page++
			if (this.page <= this.pageCount) {
				this.getList(this.page)
			}
		},
		close(e) {
			this.show = e.show
		},
		changeTab(i) {
			this.active = i
			this.getList()
		},
		save() {
			SaveBj({
				courseid: this.courseId,
				pid: this.periodId,
				Intro: this.val,
				share: this.isShare
			}).then(res => {
				if (res.result) {
					uni.showToast({
						title: '发表成功'
					})
					this.val = ''
					this.show = false
					this.getList()
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					})
				}

			})
		},
		getList(p=1){
			this.page = p
			if (this.page == 1) {
				this.list = []	
			}
			this.moreStatus = 'loading'
			GetBj({
				page: this.page,
				pagesize: 10,
				Periodid: this.periodId
			}).then(res => {
				uni.stopPullDownRefresh();
				this.pageCount = res.pagecount || 0
				if (this.page >= this.pageCount) {
					this.moreStatus = 'noMore'
				} else {
					this.moreStatus = 'more'
				}
				if (res.result) {
					res.WDbjlist.map((item)=>{
						item.$time = KS_Utils.getHistoryTime(item.adddate);
					});
					if(this.active == 0){
						this.list = this.list.concat(res.WDbjlist)	
					}else{
						this.list = this.list.concat(res.bjlist)
					}	
				}
			})
		}
	},
	mounted() {
		
		this.$store.ready(state => {
			this.state = state
			this.getList()
		})

	}
}
